import React, { Component } from 'react';
import './App.css';

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      display: 0,
      groupOne: 0,
      groupTwo: 0
    }
    this.clearScreen = this.clearScreen.bind(this)
    this.numberClick = this.numberClick.bind(this)
    this.operationClick = this.operationClick.bind(this)
  }

  clearScreen() {
    this.setState({display: 0})
  }

  numberClick(e) {
    if (this.state.display === 0) {
      this.setState({display: e.target.innerHTML})
    } else {
      this.setState({
        display: this.state.display + e.target.innerHTML
      })
    }
  }

  operationClick(e) {
    if (this.state.display === 0) {
      this.setState({display: 0})
    } else {
      this.numberClick(e)
    }
  }

  render() {
    console.log(this.state.display)
    return (
      <div className="App">
        <div>
          <h1
            id="display">{this.state.display}</h1>
        </div>
        <div>
          <button
            id="clear"
            onClick={this.clearScreen}
          >
          AC
          </button>

          <button
            id="divide"
            onClick={this.operationClick}
          >
          /
          </button>

          <button
            id="multiply"
            onClick={this.operationClick}
          >
          *
          </button>

          <button
            id="seven"
            onClick={this.numberClick}
          >
          7
          </button>

          <button
            id="eight"
            onClick={this.numberClick}
          >
          8
          </button>

          <button
            id="nine"
            onClick={this.numberClick}
          >
          9
          </button>

          <button
            id="subtract"
            onClick={this.operationClick}
          >
          -
          </button>

          <button
            id="four"
            onClick={this.numberClick}
          >
          4
          </button>

          <button
            id="five"
            onClick={this.numberClick}
          >
          5
          </button>

          <button
            id="six"
            onClick={this.numberClick}
          >
          6
          </button>

          <button
            id="add"
            onClick={this.operationClick}
          >
          +
          </button>

          <button
            id="one"
            onClick={this.numberClick}
          >
          1
          </button>

          <button
            id="two"
            onClick={this.numberClick}
          >
          2
          </button>

          <button
            id="three"
            onClick={this.numberClick}
          >
          3
          </button>

          <button
            id="equals"
            onClick={this.operationClick}
          >
          =
          </button>

          <button
            id="zero"
            onClick={this.numberClick}
          >
          0
          </button>

          <button
            id="decimal"
            onClick={this.numberClick}
          >
          .
          </button>
        </div>
      </div>
    );
  }
}

export default App;